import request from '@/utils/request'

export function navList() {
  return request({
    url: '/app/image/nav/list',
    method: 'get'
  })
}

export function imageList({ pageNo, pageSize, navId }) {
  return request({
    url: `/app/image/nav/${navId}/image`,
    method: 'get',
    params: {
      pageNo,
      pageSize
    }
  })
}
