<template>
  <div :key="id" class="album-item-container">
    <v-pagination
      v-model="page.no"
      :length="lengthVal"
      :total-visible="7"
      @input="handlePageNo"
    />
    <!-- component -->
    <viewer class="album-item-box" :images="getSrcOriList(data)" :options="options">
      <div
        v-for="src in getSrcList(data)"
        :key="src"
        class="img-item-box"
      >
        <img
          alt="图片"
          :src="src"
          class="image-item"
          :data-src="src+'?type=m'"
        >
      </div>
    </viewer>
  </div>
</template>

<script>
import { imageList } from '@/api/modules/album'

export default {
  name: 'Nav',
  props: {
    id: {
      require: true,
      type: Number,
      default: null
    }
  },
  data() {
    return {
      data: [],
      page: {
        no: 1,
        size: 10,
        total: 0
      },
      options: {
        url: 'data-src',
        navbar: false
      }
    }
  },
  computed: {
    lengthVal() {
      if (this.total === 0) return 0
      return Math.floor(this.page.total / this.page.size) + 1
    }
  },
  mounted() {
    this.initData()
  },
  methods: {
    initData() {
      imageList({
        pageNo: this.page.no,
        pageSize: this.page.size,
        navId: this.id
      }).then(_data => {
        this.$set(this, 'data', _data.data.data)
        this.page.total = _data.data.total
        console.log(this.data)
      })
    },
    getSrcOriList(data) {
      const result = []
      data.forEach(item => {
        result.push(item.uri + '?type=ori')
      })
      return result
    },
    getSrcList(data) {
      const result = []
      data.forEach(item => {
        result.push(item.uri)
      })
      return result
    },
    handlePageNo() {
      this.initData()
    }
  }
}
</script>

<style lang="scss" scoped>
.album-item-box {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

  .img-item-box {
    height: 320px;
    width: 300px;
    line-height: 300px;

    .image-item {
      max-width: 300px;
      margin: 0 auto;
      max-height: 300px;
      vertical-align: middle;
    }
  }
}
</style>
