<template>
  <div class="album-container">
    <div v-for="item in navList" :key="item.id">
      <div>
        {{ item.name }}
      </div>
      <Nav :id="item.id" />
    </div>
  </div>
</template>

<script>
import { navList } from '@/api/modules/album'
import Nav from '@/views/modules/album/components/Nav'

export default {
  name: 'AlbumIndex',
  components: { Nav },
  data() {
    return {
      navList: [],
      data: [],
      page: {
        no: 0,
        size: 10,
        total: 0
      }
    }
  },
  created() {
    this.initData()
  },
  methods: {
    initData() {
      navList().then(data => {
        this.navList = data.data
      })
    }
  }
}
</script>

